@tailwind base;
@tailwind components;
@tailwind utilities;
@config "../tailwind.config.js";

.az-logo {
  font-weight: 700;
  font-size: 28px;
  font-family: Poppins, sans-serif;
  text-transform: lowercase;
  color: #5b47fb;
  letter-spacing: -1px;
  display: flex;
  align-items: center;
  position: relative;
  top: -2px;
}


@keyframes scale {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-scale {
  animation: scale 0.3s ease forwards;
}

@keyframes shake {
  10%,
  90% {
    transform: translateX(-1px);
  }

  20%,
  80% {
    transform: translateX(2px);
  }

  30%,
  50%,
  70% {
    transform: translateX(-4px);
  }

  40%,
  60% {
    transform: translateX(4px);
  }
}

.animate-shake {
  animation: shake 0.3s;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
  }
  50% {
    transform: none;
  }
}

.animate-bounce {
  animation: bounce 1s cubic-bezier(0.8, 0, 1, 1) infinite;
}

@keyframes successCircle {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes successCheck {
  0% {
    stroke-dashoffset: 100;
    opacity: 0;
  }
  60% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}

.success-circle {
  animation: successCircle 0.6s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
}

.success-check {
  stroke-dasharray: 100;
  stroke-dashoffset: 100;
  animation: successCheck 0.9s cubic-bezier(0.22, 0.61, 0.36, 1) forwards;
  animation-delay: 0.3s;
}

.connect-btn {
  transition: all 0.2s ease;
}

.connect-btn:hover {
  transform: scale(1.05);
}

.connect-btn:active {
  transform: scale(0.95);
}

.step-view {
  display: none;
}

.verification-input {
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
  border-radius: 0.5rem;
  font-size: 1.125rem;
  font-weight: 600;
  border: 1px solid rgb(209 213 219);
}

.verification-input:focus {
  outline: none;
  border-color: #5B47FB;
  box-shadow: 0 0 0 2px rgba(91, 71, 251, 0.2);
}

.verification-input.filled {
  background-color: rgb(249 250 251);
  border-color: #5B47FB;
}

.verification-input.error {
  border-color: rgb(239 68 68);
  animation: shake 0.3s;
}

.verification-button {
  display: block;
  width: 100%;
  text-align: center;
  color: #5B47FB;
  font-weight: 500;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: color 0.2s;
}

.verification-button:hover {
  color: #4936E8;
  background-color: rgba(91, 71, 251, 0.05);
}

@keyframes overlayFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes overlayContentSlideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes logoFloat {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes pulseGlow {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(91, 71, 251, 0.4);
  }
  50% {
    box-shadow: 0 0 20px 3px rgba(91, 71, 251, 0.2);
  }
}

.redirect-overlay-enter {
  animation: overlayFadeIn 0.3s ease-out;
}

.redirect-overlay-content {
  animation: overlayContentSlideUp 0.4s ease-out;
}

.redirect-logo-container {
  animation: logoFloat 2s ease-in-out infinite, pulseGlow 2s ease-in-out infinite;
}

.animate-delay-100 {
  animation-delay: 0s !important;
}

.animate-delay-200 {
  animation-delay: 0.4s !important;
}

.animate-delay-300 {
  animation-delay: 0.8s !important;
}

@keyframes pulseFlow {
  0%, 100% {
    transform: scale(0.9);
    opacity: 0.4;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-pulse-flow {
  animation: pulseFlow 2s ease-in-out infinite;
}


/* Skeleton styles */
.skeleton {
  background-color: #e2e8f0; /* Tailwind's gray-200 */
  position: relative;
  overflow: hidden;
  border-radius: 6px;
}

.skeleton::after {
  content: '';
  position: absolute;
  top: 0;
  left: -150px;
  height: 100%;
  width: 150px;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  animation: loading 1.2s infinite;
}

@keyframes loading {
  0% {
    left: -150px;
  }
  100% {
    left: 100%;
  }
}

.skeleton-text {
  height: 1rem;
  margin-bottom: 0.5rem;
}

.skeleton-circle {
  width: 2rem;
  height: 2rem;
  border-radius: 9999px;
}

.skeleton-button {
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 0.5rem;
}

.skeleton-card {
  padding: 0.75rem;
  border: 1px solid #e5e7eb; /* gray-200 */
  border-radius: 0.5rem;
  display: flex;
  gap: 0.75rem;
  align-items: center;
}


.fade-in {
  -webkit-animation: fade-in .5s steps(20, end) both;
  animation: fade-in .5s steps(20, end) both;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
